import React,{useState} from 'react';
import Alltodos from './Alltodos';
const App=()=>{
    const [id,setId]=useState(1);
   const [todos,addTodo]=useState([]);
   const [name,setName]=useState('');
   const [age,setAge]=useState('');
   const [Error,setError]=useState('');

   const onSubmit =(e)=>{
     e.preventDefault();
     if(name!=='' && age!==''){
     const newtodo ={
                    'id':id,
                     'name':name,
                     'age':age
                     }
     addTodo([...todos,newtodo]);
     setId(id+1);
     setName('');
     setAge('');
     setError('');
     }else{
         setError('Please check your informations....');
         }

   }
   const Delete =id =>{
       let i = todos.findIndex(todo=>todo.id===id);
       todos.splice(i,1);
       addTodo([...todos]);
       console.log(todos)
    
   }
    return (
        <div className="container">
        <center><h1>TodoList App </h1></center>
        <div className="row py-3">
        <div className="col-md-5 mx-1">
        <p className="error" style={{'color':'red','fontSize':'15px','textAlign':'center'}}>{Error}</p>
        <form onSubmit={onSubmit}>
           <h4>Name :</h4>
             <input type="text" value={name}
              className="form-control" 
               onChange={e=>setName(e.target.value)} />
            <h4>Age :</h4> 
              <input type="number" value={age}
              className="form-control" 
              onChange={e=>setAge(e.target.value)} />

              <input type="submit" className="btn btn-primary my-3" value="Add To List" />
        </form>
        </div>
        <div className="col-md-6 py-7" style={{'border':'1px solid red'}}>
        {todos.length!==0 ? <Alltodos todos={todos} Delete={Delete} />
        :
        <h4 style={{'textAlign':'center'}}>There is no item...</h4>}
        </div>
        </div>
        </div>
           )
}
export default App;